  <div
    v-if="post.id"
    :class="{ 'post-item-wrapper': postData.childrens?.length && !isPostPage }"
  >
    <div
      :id="'post-' + post.id"
      ref="target"
      class="post-item"
      :data-href="`${host}/posts/${post.id}`"
    >
      <div v-if="!telegramLoaded" class="post-loader-box" />

      <div class="head">
        <a
          class="owner-link"
          :href="`${
            postData.type === 'telegram'
              ? 'https://t.me/s/'
              : 'https://twitter.com/'
          }${postData.telegramName || postData.channelNickname}`"
          target="_blank"
        >
          <div class="owner-card">
            <div
              class="bg-img"
              :style="{ 'background-image': `url(${postData.ownerLogoUrl})` }"
            />
            <div
              class="bg-placeholder"
              :style="{ 'background-color': `rgba(${randomRgb()}, 0.8)` }"
            >
              <p>{{ postData.messageOwner.slice(0, 1) }}</p>
            </div>
          </div>

          <div class="author">
            <p style="text-decoration: none">
              <span v-if="postData.messageOwner.includes(' ')">
                {{
                  postData.messageOwner.length > 30
                    ? postData.messageOwner.substring(0, 40) + '...'
                    : postData.messageOwner
                }}
              </span>
              <span v-else>
                {{
                  postData.messageOwner.length > 14
                    ? postData.messageOwner.substring(0, 14) + '...'
                    : postData.messageOwner
                }}
              </span>
            </p>
          </div>
        </a>
        <button
          class="unsubcribe-btn"
          @click.prevent="toggleChannelSubscribe()"
        >
          {{ postData.subscribed ? $t('subscribe') : $t('unsubscribe') }}
        </button>
      </div>
      <img
        v-if="
          postData.previewUrl &&
          !postData.imgsList.length &&
          !postData.video &&
          !isOlder
        "
        class="preview-image"
        loading="lazy"
        :src="postData.previewUrl"
        alt="превью"
        @click="zoomPreview = true"
      />
      <div
        v-else-if="
          !(
            postData.previewUrl &&
            !postData.imgsList.length &&
            !postData.video
          ) &&
          !postData.video &&
          !isOlder
        "
        class="imgs-list"
      >
        <div :id="'swiper-slider-' + post.id">
          <div class="swiper-wrapper">
            <div
              v-for="(img, index) in postData.imgsList"
              :key="img"
              class="swiper-slide"
            >
              <img
                class="preview-image"
                :src="img"
                alt="превью"
                loading="lazy"
                @click="toggleLightbox(true, index)"
              />
            </div>
          </div>
          <div v-if="postData.imgsList.length" class="swiper-pagination" />
        </div>
        <vue-easy-lightbox
          :visible="zoomPreview"
          :imgs="postData.imgsList"
          :index="indexRef"
          @hide="toggleLightbox(false)"
        />
      </div>

      <vue-easy-lightbox
        v-if="postData.previewUrl && !postData.imgsList.length"
        :visible="zoomPreview"
        :imgs="[postData.previewUrl]"
        @hide="zoomPreview = false"
      />

      <div class="content-box" :class="{ loaded: telegramLoaded }">
        <div class="telegram-frame" :class="{ isOlder: isOlder }" />
        <div
          v-if="!isOlder"
          class="content"
          v-html="postData.originalContent"
        />
        <div v-if="postData.originalContent"></div>
        <div v-if="isVisiblePublicContent">
          <br />
          <b>{{ $t('translate') }}:</b>
          <br />
          <br />
          <div class="content" v-html="postData.publicContent" />
          <br />
        </div>

        <div
          class="footer"
          :class="{ 'single-footer': postUrl === locationHref }"
        >
          <div class="footer-bottom">
            <!-- AddToAny BEGIN -->
            <div class="footer-box">
              <div class="footer-bottom-share-box">
                <p
                  v-if="isVisiblePublicContent"
                  style="font-size: 12px"
                  class="translated-with"
                >
                  {{ $t('translated_with') }}
                  <a target="_blank" href="https://translate.google.com/"
                    >google translate</a
                  >. {{ $t('can_has_errors') }}
                </p>
                <a
                  v-else-if="
                    postData.originalContent && !isVisiblePublicContent
                  "
                  href="#"
                  @click.prevent="translateContent()"
                  >{{ $t('show_translate') }}</a
                >
                <!-- <p class="share-title">{{ $t('share') }}:</p>
                <div
                  class="a2a_kit a2a_kit_size_32 a2a_default_style"
                  :data-a2a-url="postUrl"
                  :data-a2a-title="textContent"
                >
                  <a class="a2a_button_facebook" />
                  <a class="a2a_button_twitter" />
                  <a class="a2a_button_viber" />
                  <a class="a2a_button_linkedin" />
                  <a class="a2a_button_telegram" />
                  <a class="a2a_button_facebook_messenger" />
                </div> -->
              </div>
              <div class="footer-bottom-info">
                <p class="post-source">
                  <a
                    target="_blank"
                    :href="`https://t.me/${
                      postData.telegramName || postData.channelNickname
                    }/${post.id.replace(/\D/g, '')}`"
                    >{{ $t('source') }}</a
                  >
                </p>
                <p class="date">
                  {{ date }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="postData.childrens?.length" class="copy-posts">
      {{ console.log(postData) }}
      <q-slide-transition appear>
        <div class="copy-posts" v-if="isCopyPostsBoxVisible">
          <post-item
            v-for="children in postData.childrens"
            :key="children.id"
            :post="children"
          />
        </div>
      </q-slide-transition>
      <button
        @click.prevent="toggleCopyPostsBox()"
        class="copy-posts-hide-btn btn"
      >
        {{
          `${isCopyPostsBoxVisible ? '-' : '+'}(${postData.childrens.length})`
        }}
      </button>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { extractTextFromHtml, replaceTagWithInnerText } from '@/utils';
import xss from 'xss';
import { useStore } from 'vuex';
import VueEasyLightbox, { useEasyLightbox } from 'vue-easy-lightbox';
import { ChannelService } from '@/services';
import Swiper, { Navigation, Pagination } from 'swiper';
import { PostsCounterService } from '@/services';
import { useQuasar } from 'quasar';
import { useI18n } from 'vue-i18n';
import { TranslateService } from 'src/services';
import { getLinkPreview, getPreviewFromContent } from 'link-preview-js';

// configure Swiper to use modules
Swiper.use([Navigation, Pagination]);

const sliderConfig = {
  // Optional parameters
  direction: 'horizontal',
  autoHeight: false,
  centeredSlides: true,
  autoplay: false,
  loop: false,
  slidesPerView: 1,
  spaceBetween: 0,
};

export default {
  components: {
    VueEasyLightbox,
  },
  props: {
    post: {
      type: Object,
      default: () => {},
    },
    isPostPage: {
      type: Boolean,
      default: false,
    },
  },

  setup(props) {
    const telegramLoaded = ref(true);
    const content = ref(null);
    const zoomPreview = ref(false);
    const postData = ref(props.post);
    const postUrl = `${process.env.VUE_APP_HOST}/posts/${postData.value.id}`;
    const locationHref =
      typeof window !== 'undefined' ? window.location.href : postUrl;
    const target = ref(null);
    const isCopyPostsBoxVisible = ref(false);
    const { locale } = useI18n({ useScope: 'global' });

    const $q = useQuasar();

    const postsCounterService = new PostsCounterService(useStore());

    postData.value.subscribed = true;

    const lightbox = useEasyLightbox({
      initIndex: 0,
    });

    const toggleLightbox = (isShow, index = 0) => {
      lightbox.changeIndex(index);
      zoomPreview.value = isShow;
    };

    const toggleChannelSubscribe = async () => {
      postData.value.subscribed = !postData.value.subscribed;

      postsCounterService.clearPostsCountInterval();

      const nickname =
        postData.value.telegramName || postData.value.channelNickname;

      ChannelService.updateChannel(nickname, {
        active: postData.value.subscribed,
      });

      $q.notify({
        position: 'bottom-left',
        message: `${$t('you')} ${
          postData.value.subscribed ? $t('subscribed') : $t('unsubscribed')
        } @${nickname}`,
      });
    };

    const date = String(
      new Date(postData.value.date).toLocaleDateString('uk-UA', {
        day: 'numeric',
        month: 'numeric',
        year: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
      }),
    )
      .split(', ')
      .reverse()
      .join(', ');

    const getContent = (data, lang) => {
      if (!data.value?.[lang] && lang !== 'uk') {
        return null;
      }

      const result = xss(data.value?.[lang] || data.value.content);
      const text = replaceTagWithInnerText(result, 'tg-emoji');

      return text;
    };

    const text = getContent(postData, locale.value);

    postData.value.originalContent = getContent(postData, 'uk');
    postData.value.content = text;
    content.value = text;

    postData.value.messageOwner = extractTextFromHtml(
      postData.value.messageOwner,
    );

    const loadYoutubePlayer = () => {
      const html = document.createElement('div');

      html.innerHTML = postData.value.content;

      const youtubeLink = html.querySelector('a[href^="https://youtu.be"]');

      const iframe = document.createElement('iframe');
      const source = `https://www.youtube.com/embed/${youtubeLink
        .getAttribute('href')
        .split('/')
        .pop()}`;

      iframe.setAttribute('id', `ytplayer-${postData.value.id}`);
      iframe.setAttribute('src', source);
      iframe.setAttribute(
        'allow',
        'accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture',
      );
      iframe.setAttribute('frameborder', 0);
      iframe.setAttribute('allowfullscreen', true);
      iframe.setAttribute('loading', 'lazy');

      // replace youtube link with iframe
      youtubeLink.replaceWith(iframe);

      //r Remove quotes around iframe
      postData.value.publicContent = html.innerHTML.replace(
        /&quot;<iframe>/g,
        '',
      );
      postData.value.publicContent = html.innerHTML;
    };

    const loadTelegram = () => {
      // postData.value.publicContent = null;

      const postContent = document.querySelector(
        `#post-${postData.value.id} .telegram-frame`,
      );

      const telegramScript = document.createElement('script');

      telegramScript.setAttribute(
        'src',
        'https://telegram.org/js/telegram-widget.js?21',
      );
      telegramScript.setAttribute('class', 'tg-video-widget');
      telegramScript.setAttribute(
        'data-telegram-post',
        postData.value.id.replace('-', '/'),
      );
      telegramScript.setAttribute('data-width', '100%');
      telegramScript.setAttribute('data-userpic', false);
      telegramScript.setAttribute('data-dark', 1);
      telegramScript.setAttribute('async', true);

      postContent.appendChild(telegramScript);

      telegramScript.onload = () => {
        setTimeout(() => {
          const iframe = document.querySelector(
            `#telegram-post-${props.post.id}`,
          );

          if (!iframe) {
            telegramLoaded.value = true;
            return;
          }

          iframe.setAttribute('loading', 'lazy');
          iframe.setAttribute('class', 'telegram-video');

          iframe.onload = () => {
            setTimeout(() => {
              telegramLoaded.value = true;
            }, 100 + Math.floor(Math.random() * 400));
          };
        }, 0);
      };
    };

    const isPostOlderThanOneDay = () => {
      const postDate = new Date(postData.value.date);
      const currentDate = new Date();

      return currentDate.getTime() - postDate.getTime() > 86400000;
    };

    const isOlder = isPostOlderThanOneDay();

    const isVisiblePublicContent = ref(false);
    const translateContent = async () => {
      if (isVisiblePublicContent.value) {
        return;
      }

      const to = locale.value;

      postData.value.publicContent = await TranslateService.translate({
        to,
        text: postData.value.originalContent,
      });

      isVisiblePublicContent.value = true;
    };

    onMounted(async () => {
      postData.value.ownerLogoUrl =
        ChannelService.channels[
          postData.value.telegramName || postData.value.channelNickname
        ]?.photoImage || postData.value.ownerLogoUrl;

      // if (typeof window !== 'undefined' && window?.a2a) {
      //   a2a.init_all();
      // }

      const getFirstUrlInContent = (content) => {
        if (!content) {
          return;
        }

        const urls = content.split(' ').map((line) => {
          const url = line.match(
            /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)/g,
          );

          return url;
        });

        return urls.filter((url) => url)[0][0];
      };

      // console.log(getFirstUrlInContent(postData.value.originalContent && postData.value.content));
      // console.log(await getPreviewFromContent(postData.value.originalContent));

      if (
        postData.value.content &&
        postData.value.content.includes('<a href="https://youtu.be')
      ) {
        loadYoutubePlayer();
      } else {
        postData.value.publicContent = postData.value.content;
      }

      if (postData.value.video || isOlder) {
        loadTelegram();
      }

      if (postData.value.imgsList.length > 1) {
        sliderConfig.pagination = {
          el: '.swiper-pagination',
          clickable: true,
        };

        // init Swiper after all image loaded
        const images = document.querySelectorAll(
          `#post-${postData.value.id} .preview-image`,
        );

        let loadedImages = 0;

        images.forEach((img) => {
          img.onload = () => {
            loadedImages += 1;

            if (loadedImages === images.length) {
              // set swiper height like image with max height
              const maxHeight = Math.max(
                ...Array.from(images).map((img) => img.height),
              );

              sliderConfig.height = maxHeight;

              new Swiper('#swiper-slider-' + postData.value.id, sliderConfig);
            }
          };
        });
      }
    });

    const toggleCopyPostsBox = async () => {
      isCopyPostsBoxVisible.value = !isCopyPostsBoxVisible.value;
    };

    const randomRgb = () => {
      const o = Math.round,
        r = Math.random,
        s = 255;
      return `${o(r() * s)},${o(r() * s)},${o(r() * s)}`;
    };

    return {
      host:
        typeof window !== 'undefined'
          ? window.location.origin
          : process.env.VUE_APP_HOST,
      indexRef: lightbox.indexRef,
      textContent: extractTextFromHtml(postData.value.content),
      originalContent: extractTextFromHtml(postData.value.originalContent),
      isVisiblePublicContent,
      isCopyPostsBoxVisible,
      isOlder,
      target,
      postUrl,
      postData,
      date,
      content,
      telegramLoaded,
      zoomPreview,
      locationHref,
      toggleLightbox,
      toggleChannelSubscribe,
      toggleCopyPostsBox,
      randomRgb,
      translateContent,
    };
  },
};
</script>

